.app {
  height: 100vh;
  width: 100vw;
}

.header {
  background-color: teal;
  width: 100%;
  height: 5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content {
  width: 100%;
  height: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  
}

.inputbox {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer {
  width: 100%;
  height: 5%;
  display: flex;
  justify-content: center;
  background-color: teal;
  align-items: center;
}

.word {
  margin-block: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hangmanSection {
  display: flex;
  height: 50%;
  margin-top: 10px;
  width: 98%;
  justify-content: space-around;
  align-items: center;
}

.errorMessage{
  color:red;
  margin:10px;
}

.successMessage{
  color:teal;
  margin:10px;
}

.reset-button{
  background-color:teal;
  font-size: 20px;
  color:azure;
  padding: 10px;
  border: none;
  margin-top: 15px;

}